import {useSelector, useDispatch} from 'react-redux';
import {setArrowBack} from '../../redux/actions/stateAction'
import { useNavigate } from "react-router-dom";

function Header(props) {
   
let level;
const dispatch = useDispatch();
const navigate = useNavigate();

const state = useSelector((store)=>store.state)

function handleBack(){
   dispatch (setArrowBack(false))
   navigate('/')
}

 switch (props.level){
   case 'light' : level =' ';break;
   case 'average': level =' '; break;
   case 'hard': level=' ';break;
} 

   return (
    <div className='header'>
      {!state.arrowBack? ' ' :
         <div>
            <img onClick={()=> handleBack()} className='arrow_back' src='https://i.postimg.cc/2StmCpGP/icons8-24.png'>
            </img>
           <span className='header__back'>  /  : </span>
                  {level}
            </div>}
    </div> 
   )
}

export default Header;